html {
  font-size: 10px;
}

body {
  color: #000;
  font-family: Open Sans, sans-serif;
  font-style: normal;
  font-weight: 400;
}

.status {
  color: red;
  display: block;
}

a:hover {
  text-decoration: none;
}

a:focus, button:focus, input:focus, textarea:focus {
  outline: none;
}

.no-padding {
  padding: 0 !important;
}

.form {
  text-align: center;
  background-color: #fff;
  border-radius: 1rem;
  padding: 2rem 5rem;
}

.form h2 {
  color: #333;
  margin-bottom: 3rem;
  font-family: Open Sans, sans-serif;
  font-size: 2rem;
  font-style: normal;
  font-weight: 700;
  line-height: 1.5;
}

.form h2 span {
  text-transform: uppercase;
  font-size: 2.4rem;
}

.form_input {
  height: 5.8rem;
  text-align: center;
  border-radius: 1rem;
  margin-bottom: 2rem;
  padding: 0;
}

.form_input::-webkit-input-placeholder {
  color: #999;
  opacity: 1;
  font-size: 1.5rem;
}

.form_input::-moz-placeholder {
  color: #999;
  opacity: 1;
  font-size: 1.5rem;
}

.form_input:-moz-placeholder {
  color: #999;
  opacity: 1;
  font-size: 1.5rem;
}

.form_input:-ms-input-placeholder {
  color: #999;
  opacity: 1;
  font-size: 1.5rem;
}

.form_input:focus::-webkit-input-placeholder {
  color: #0000;
}

.form_input:focus::-moz-placeholder {
  color: #0000;
}

.form_input:focus:-moz-placeholder {
  color: #0000;
}

.form_input:focus:-ms-input-placeholder {
  color: #0000;
}

.form_notice {
  color: #666;
  margin-top: 1rem;
  margin-bottom: 2rem;
  font-size: 1.1rem;
}

.button {
  background: linear-gradient(#ffed26 0%, #ffb400 100%);
  border: 1px solid #ffc600;
  border-radius: 1rem;
  padding: 2.5rem 0;
  font-family: Open Sans, sans-serif;
  font-size: 1.7rem;
  font-style: normal;
  font-weight: 700;
  box-shadow: 1px 2px 20px #f906;
}

.button:hover {
  cursor: pointer;
}

.section_header {
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 8rem;
}

.section_header h2 {
  color: #333;
  font-family: Open Sans, sans-serif;
  font-size: 3rem;
  font-style: normal;
  font-weight: 700;
  line-height: 1.4;
}

.section_header_sub {
  width: 17rem;
  height: .2rem;
  background: url("line.bcae57ba.png") no-repeat;
  margin-top: 2rem;
  margin-left: 50%;
  transform: translateX(-50%);
}

.arrow {
  z-index: 99;
  width: 2rem;
  height: 2rem;
  border: none;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.prev {
  background: url("left_arrow.dcb5fd28.png") center / 70% no-repeat;
  left: 0;
}

.next {
  background: url("right_arrow.3b7d9d77.png") center / 70% no-repeat;
  right: 0;
}

.header {
  height: 10rem;
  background-color: #fff;
}

.header .logo {
  height: 10rem;
  display: table;
}

.header .logo_img {
  vertical-align: middle;
  display: table-cell;
}

.header .logo_img img {
  margin-right: 2rem;
}

.header .logo p {
  vertical-align: middle;
  color: #0089cd;
  font-size: 1.4rem;
  line-height: 1.2;
  display: table-cell;
}

.header_btn {
  color: #55a831;
  background: #fff url("edit.406b1dd4.png") 1rem no-repeat;
  border: 2px solid #55a831;
  border-radius: 1rem;
  padding: 1rem 1rem 1rem 5rem;
  font-family: Open Sans, sans-serif;
  font-size: 1.4rem;
  font-style: normal;
  font-weight: 700;
  line-height: 1.2;
}

.header_btn_wrap {
  width: 100%;
  height: 10rem;
  display: table;
}

.header_btn_wrap_block {
  vertical-align: middle;
  display: table-cell;
}

.header_btn_wrap_block img {
  margin-right: .7rem;
}

.header_btn:hover {
  cursor: pointer;
}

.header .working_hours {
  height: 10rem;
  display: table;
}

.header .working_hours_img {
  vertical-align: middle;
  display: table-cell;
}

.header .working_hours_img img {
  margin-right: 2rem;
}

.header .working_hours p {
  vertical-align: middle;
  color: #333;
  font-size: 1.4rem;
  display: table-cell;
}

.header .working_hours p span {
  font-family: Open Sans, sans-serif;
  font-size: 1.8rem;
  font-style: normal;
  font-weight: 700;
}

.header .contact_us {
  height: 10rem;
  text-align: right;
  display: table;
}

.header .contact_us_wrap {
  vertical-align: middle;
  line-height: 1.7;
  display: table-cell;
}

.header .contact_us_wrap a {
  color: #333;
  font-family: Open Sans, sans-serif;
  font-size: 2.3rem;
  font-style: normal;
  font-weight: 700;
  display: block;
}

.header .contact_us_wrap a img {
  margin-right: 1rem;
}

.header .contact_us_wrap .phone_link {
  color: #0089cd;
  font-family: Open Sans, sans-serif;
  font-size: 1.5rem;
  font-style: normal;
  font-weight: 400;
  text-decoration: underline;
}

.main {
  background: #fff url("bg.8ec632f0.png") no-repeat;
  padding: 10rem 0;
}

.main h1 {
  color: #333;
  margin: 0;
  font-family: Open Sans, sans-serif;
  font-size: 5.5rem;
  font-style: normal;
  font-weight: 700;
  line-height: 1.5;
}

.main h1 span {
  font-size: 2.6rem;
}

.main_features {
  margin-top: 13rem;
  padding: 0;
}

.main_features_block {
  text-align: center;
}

.main_features_block .small_img {
  margin-top: 1rem;
}

.main_features_block p {
  text-transform: uppercase;
  color: #fff;
  margin-top: -6rem;
  font-family: Open Sans, sans-serif;
  font-size: 1.3rem;
  font-style: normal;
  font-weight: 700;
  line-height: 1.3;
}

.main_form {
  margin-top: 2rem;
}

.glazing {
  padding: 8rem 0;
}

.glazing_block {
  display: inline-block;
}

.glazing_block img {
  margin: 0 auto 2rem;
  display: block;
}

.glazing_block a {
  font-size: 1.8rem;
  font-weight: 600;
  text-decoration: underline;
}

.glazing_block a:hover {
  cursor: pointer;
}

.glazing_cold {
  text-align: center;
  background: linear-gradient(#c7dce9 0% 0%, #fff0 100%);
  border-top: .5rem solid #0089cd;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  margin-top: 4rem;
  padding: 2.5rem 0;
}

.glazing_cold h3 {
  text-transform: uppercase;
  color: #0089cd;
  margin-bottom: 2.5rem;
  font-family: Open Sans, sans-serif;
  font-size: 2rem;
  font-style: normal;
  font-weight: 700;
}

.glazing_warm {
  text-align: center;
  background: linear-gradient(#f7eac9 0% 0%, #fff0 100%);
  border-top: .5rem solid #ffb903;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  margin-top: 4rem;
  padding: 2.5rem 0;
}

.glazing_warm h3 {
  text-transform: uppercase;
  color: #ffb903;
  margin-bottom: 2.5rem;
  font-family: Open Sans, sans-serif;
  font-size: 2rem;
  font-style: normal;
  font-weight: 700;
}

.glazing ul {
  text-align: left;
  margin-top: 3.7rem;
  padding: 0 6rem;
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 2.1;
  list-style: none;
}

.glazing ul li:before {
  content: "";
  width: 2.5rem;
  height: 2.5rem;
  vertical-align: middle;
  background: url("check.ecddd677.png") center / 100% no-repeat;
  margin-right: 1.7rem;
  display: inline-block;
}

.glazing_price {
  padding: 0 6rem;
}

.glazing_price p {
  vertical-align: middle;
  margin-right: 3rem;
  font-family: Open Sans, sans-serif;
  font-size: 2rem;
  font-style: normal;
  font-weight: 700;
  line-height: 1.7;
  display: inline-block;
}

.glazing_price p span {
  font-family: Open Sans, sans-serif;
  font-size: 1.4rem;
  font-style: normal;
  font-weight: 400;
}

.glazing_price_btn {
  padding: 2rem;
}

.glazing .aluminum, .glazing .plastic, .glazing .french, .glazing .rise {
  display: none;
}

.glazing a.active {
  color: #454545;
  text-decoration: none;
}

.decoration {
  background-color: #f7f7f7;
  padding: 8rem 0;
}

.decoration_item {
  text-align: center;
}

.decoration_item a {
  color: #000;
  text-transform: uppercase;
  border-bottom: 1px dotted #000;
  font-size: 2rem;
  font-weight: 600;
}

.decoration_item a:hover {
  cursor: pointer;
}

.decoration_item a:focus {
  color: #0089cd;
  border: none;
  text-decoration: none;
}

.decoration_item .no_click {
  height: 8rem;
  border-top: 1rem solid #f7f7f7;
  padding: 2rem 0;
}

.decoration_item .after_click {
  height: 8rem;
  background: linear-gradient(#ebebeb 0% 0%, #fff0 100%);
  border-top: 1rem solid #0089cd;
  padding: 2rem 0;
}

.decoration_content {
  margin-top: 8rem;
}

.decoration_content_material img {
  margin-top: 2.5rem;
}

.decoration_content_material h3 {
  min-height: 4rem;
  font-size: 1.4rem;
  font-weight: 600;
  line-height: 1.4;
}

.decoration_content_material p {
  color: #0089cd;
  font-family: Open Sans, sans-serif;
  font-size: 1.4rem;
  font-style: normal;
  font-weight: 700;
}

.decoration_content_material p span {
  color: #000;
  font-family: Open Sans, sans-serif;
  font-size: 1.2rem;
  font-style: normal;
  font-weight: 400;
  display: block;
}

.decoration .external, .decoration .rising, .decoration .roof {
  display: none;
}

.works {
  padding: 5rem 0;
}

.works img {
  margin-bottom: 3rem;
}

.works a[data-fancybox="gallery"] .lupa {
  opacity: 0;
  transition: all .2s;
  top: 40%;
  left: 50%;
  transform: translateX(-50%);
}

.works a[data-fancybox="gallery"]:hover .lupa {
  opacity: 1;
}

.guarantees {
  background-color: #0089cd;
  padding: 6rem 0 1rem;
}

.guarantees_header h2 {
  color: #fff;
}

.guarantees_header_sub {
  background: url("header_sub.95625988.png") no-repeat;
}

.guarantees h3 {
  color: #fff;
  margin-top: 3rem;
  margin-bottom: 8rem;
  font-size: 1.8rem;
  font-weight: 600;
  line-height: 1.4;
}

.guarantees img {
  min-height: 7rem;
}

.guarantees_block:hover {
  box-shadow: 0 14px 28px #00000040, 0 10px 10px #00000038;
}

.payment {
  padding: 6rem 0;
}

.payment_header {
  text-transform: none;
}

.payment_item h3 {
  font-size: 2rem;
  display: inline-block;
}

.payment_img {
  display: inline-block;
}

.payment_img img {
  margin-right: 2rem;
}

.sale {
  background: #fff url("bg.54701334.jpg") center no-repeat;
  padding: 8rem 0;
}

.sale_title {
  text-transform: uppercase;
  color: #0089cd;
  margin-bottom: 8rem;
  font-family: Open Sans, sans-serif;
  font-size: 4.8rem;
  font-style: normal;
  font-weight: 700;
}

.sale_subtitle {
  margin-bottom: 8rem;
  font-size: 2.4rem;
  line-height: 1.6;
}

.timer1 {
  color: #000;
  text-align: center;
  width: 360px;
  height: 150px;
  background-color: #fff;
  border: 1px solid gray;
  border-radius: 10px;
  padding: 10px;
}

.container1 {
  display: inline-block;
}

.numbers1 {
  float: left;
  color: #fff;
  margin-right: 10px;
  font-size: 40px;
  display: block;
}

.timer1 span {
  background-color: #575757;
  border-radius: 7px;
  margin-right: 2px;
  padding: 4px;
}

.description1 {
  text-align: center;
  color: #000;
  font-size: 18px;
}

.contacts {
  padding: 6rem 0;
}

.contacts_info {
  margin-top: -3rem;
}

.contacts_info h3 {
  color: #0089cd;
  margin-bottom: 1.8rem;
  font-size: 1.5rem;
  font-weight: 600;
}

.contacts_info p {
  margin-bottom: 3rem;
  font-size: 1.8rem;
  font-weight: 600;
}

.contacts_info p span {
  font-family: Open Sans, sans-serif;
  font-style: normal;
  font-weight: 400;
}

.contacts_info a {
  color: #000;
  margin-bottom: 3rem;
  font-size: 1.8rem;
  font-weight: 600;
  text-decoration: underline;
  display: block;
}

.contacts .map {
  width: 100%;
}

.feedback {
  background-color: #0089cd;
  padding: 6rem 0;
}

.feedback_block h3 {
  color: #fff;
  margin-right: 4rem;
  font-size: 3rem;
  display: inline-block;
}

.feedback_block a {
  color: #fff;
  font-size: 3rem;
  font-weight: 600;
  text-decoration: underline;
}

.footer {
  padding: 4rem 0;
}

.footer .copyright p {
  font-size: 1.5rem;
  line-height: 1.6;
}

.footer .logo {
  text-align: center;
}

.footer .contacts {
  text-align: right;
  margin: auto;
  padding: 0;
}

.footer .contacts a {
  display: block;
}

.footer .contacts .phone {
  color: #333;
  font-family: Open Sans, sans-serif;
  font-size: 1.8rem;
  font-style: normal;
  font-weight: 700;
}

.footer .contacts .email {
  color: #0089cd;
  font-size: 1.4rem;
  text-decoration: underline;
}

.popup, .popup_engineer, .popup_calc_end {
  width: 100%;
  height: 100%;
  z-index: 9;
  background-color: #00000080;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.myimage {
  width: 500px;
  height: 500px;
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
}

.overlay {
  width: 100%;
  height: 100%;
  z-index: 9;
  opacity: 1;
  background-color: #000000b3;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.popup_content, .popup_engineer_content, .popup_calc_end_content {
  width: 40rem;
  background-color: #fff;
  position: fixed;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
}

.popup_close, .popup_engineer_close, .popup_calc_end_close {
  color: #fff;
  background: none;
  border: none;
  font-size: 4rem;
  position: absolute;
  top: -2.2rem;
  right: -5rem;
}

.popup_calc {
  width: 100%;
  height: 100%;
  z-index: 9;
  background-color: #00000080;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.popup_calc_content {
  width: 44rem;
  background-color: #fff;
  padding: 1rem .5rem 4rem;
  position: fixed;
  top: 2%;
  left: 50%;
  transform: translateX(-50%);
}

.popup_calc_content h2 {
  color: #333;
  margin-bottom: 3rem;
  font-family: Open Sans, sans-serif;
  font-size: 3rem;
  font-style: normal;
  font-weight: 700;
}

.popup_calc_content h3 {
  margin-bottom: 3rem;
  font-size: 2rem;
}

.popup_calc_content .balcon_icons {
  margin-bottom: 1rem;
}

.popup_calc_content .big_img img {
  width: 30rem;
  margin-bottom: 3rem;
  display: none;
}

.popup_calc_content #type1 {
  display: inline-block;
}

.do_image_more img {
  width: 110px;
  height: 70px;
  cursor: pointer;
}

.popup_calc_close {
  color: #fff;
  background: none;
  border: none;
  font-size: 4rem;
  position: absolute;
  top: -2.2rem;
  right: -5rem;
}

.popup_calc input {
  width: 10rem;
  margin-bottom: 3rem;
  margin-right: .3rem;
  display: inline-block;
}

.popup_calc label {
  color: #999;
}

.popup_calc .multiplication {
  color: #323131;
  margin: 0 1rem;
  font-size: 2rem;
  display: inline-block;
}

.popup_calc_button {
  padding: 1rem 5rem;
}

.popup_calc_profile {
  width: 100%;
  height: 100%;
  z-index: 9;
  background-color: #00000080;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.popup_calc_profile_content {
  width: 40rem;
  background-color: #fff;
  padding: 1rem 5rem 4rem;
  position: fixed;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
}

.popup_calc_profile_content h2 {
  color: #333;
  margin-bottom: 3rem;
  font-family: Open Sans, sans-serif;
  font-size: 3rem;
  font-style: normal;
  font-weight: 700;
}

.popup_calc_profile_content h3 {
  margin-bottom: 3rem;
  font-size: 1.8rem;
}

.popup_calc_profile_content select {
  outline: none;
  margin-bottom: 1rem;
}

.popup_calc_profile_content .checkbox {
  display: none;
}

.popup_calc_profile_content .checkbox-custom {
  width: 22px;
  height: 22px;
  border: 2px solid #ccc;
  border-radius: 3px;
  position: relative;
}

.popup_calc_profile_content .checkbox-custom, .popup_calc_profile_content .label {
  vertical-align: middle;
  display: inline-block;
}

.popup_calc_profile_content .checkbox:checked + .checkbox-custom:before {
  content: "";
  display: block;
  position: absolute;
  inset: 0;
}

.checkbox-custom[id="cold"]:before {
  background: url("check.5360cfc5.png") center no-repeat;
}

.no-background {
  background: none;
}

.checkbox-custom[id="warm"]:before {
  background: url("check_warm.504c352f.png") center no-repeat;
}

.popup_calc_profile_content .label {
  z-index: 18rem;
  color: #000;
  padding: 1.3rem 2rem;
  font-size: 1.5rem;
  font-weight: 500;
}

.popup_calc_profile_content img {
  margin-left: 50%;
  display: block;
  transform: translateX(-50%);
}

.popup_calc_profile_close {
  color: #fff;
  background: none;
  border: none;
  font-size: 4rem;
  position: absolute;
  top: -2.2rem;
  right: -5rem;
}

.popup_calc_profile_button {
  margin-top: 1rem;
  margin-left: 50%;
  padding: 1rem 5rem;
  display: block;
  transform: translateX(-50%);
}

@media (max-width: 1400px) {
  .main {
    background-position: -30rem 0;
  }
}

@media (max-width: 1199px) {
  .header_btn {
    padding: 1rem 1rem 1rem 5rem;
    font-size: 1.2rem;
  }

  .header .working_hours_img img {
    margin-right: 1rem;
  }

  .header .working_hours p {
    font-size: 1.3rem;
  }

  .header .working_hours p span {
    font-family: Open Sans, sans-serif;
    font-size: 1.6rem;
    font-style: normal;
    font-weight: 700;
  }

  .header .contact_us_wrap a {
    font-size: 2rem;
  }

  .glazing_cold img, .glazing_warm img {
    width: 90%;
  }

  .glazing ul, .glazing_price {
    padding: 0 0 0 2.5rem;
  }

  .glazing_price p {
    margin-right: 2rem;
  }

  .decoration_item a {
    font-size: 1.8rem;
  }

  .payment_img img {
    margin-right: 1rem;
  }
}

@media (max-width: 991px) {
  .section_header h2 {
    font-size: 2.8rem;
  }

  .header {
    height: 12rem;
  }

  .header .logo, .header .logo p {
    margin-top: 1rem;
    display: block;
  }

  .header_btn_wrap, .header .working_hours {
    height: 12rem;
  }

  .header .working_hours_img img {
    margin-right: 1rem;
  }

  .header .working_hours p {
    font-size: 1.3rem;
  }

  .header .working_hours p span {
    font-family: Open Sans, sans-serif;
    font-size: 1.6rem;
    font-style: normal;
    font-weight: 700;
    display: block;
  }

  .header .contact_us {
    height: 12rem;
  }

  .header .contact_us_wrap a {
    font-size: 1.4rem;
  }

  .header .contact_us_wrap .phone_link {
    font-size: 1.15rem;
  }

  .main {
    background-size: cover;
    padding: 5rem 0 7rem;
  }

  .main h1 {
    text-align: center;
    font-size: 5rem;
  }

  .main_features {
    text-align: center;
    margin-top: 5rem;
    margin-bottom: 7rem;
  }

  .glazing_cold img, .glazing_warm img {
    width: 80%;
  }

  .glazing ul {
    padding: 0;
    display: inline-block;
  }

  .glazing_price {
    text-align: center;
    padding: 0;
  }

  .glazing_price p {
    margin-right: 10rem;
  }

  .decoration_img img {
    width: 100%;
  }

  .decoration_content {
    margin-top: 1rem;
  }

  .decoration_content_material img {
    margin-top: 2rem;
  }

  .decoration_content_material h3 {
    min-height: auto;
  }

  .decoration_form {
